import { useState } from 'react';

const Star = ({ filled, onClick, onHover, onLeave }) => {
  return (
    <svg
      width="46"
      height="44"
      viewBox="0 0 46 44"
      fill={filled ? "white" : "none"}
      xmlns="http://www.w3.org/2000/svg"
      onClick={onClick}
      onMouseEnter={onHover}
      onMouseLeave={onLeave}
      className="raiting-star-icon"
      style={{ cursor: "pointer", transition: "0.2s" }}
    >
      <path d="M27.2637 16.8926L27.4883 17.584H42.5732L30.957 26.0234L30.3691 26.4512L30.5938 27.1416L35.0312 40.7988L23.4141 32.3584L22.8262 31.9307L22.2383 32.3584L10.6201 40.7988L15.0586 27.1416L15.2832 26.4512L14.6953 26.0234L3.0791 17.584H18.1641L18.3887 16.8926L22.8262 3.23535L27.2637 16.8926Z" stroke="white" strokeWidth="2" />
    </svg>
  );
};

const SuccessfulOrderRatingRating = ({ value, onChange }) => {
  const [hover, setHover] = useState(0);

  return (
    <div className="successful-order-rating">
      {[1, 2, 3, 4, 5].map((num) => (
        <Star
          key={num}
          filled={num <= (hover || value)}
          onClick={() => onChange(num)}
          onHover={() => setHover(num)}
          onLeave={() => setHover(0)}
        />
      ))}
    </div>
  );
};

export default SuccessfulOrderRatingRating;